//Component
import Logo from "../components/Logo/Logo";
import AuthInput from "../components/AuthInput/AuthInput";
//react-bootstrap
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
//React
import { Link } from "react-router-dom";
import { useState } from "react";

const RegisterPage = () => {
  const [register, setRegister] = useState({
    account: "",
    name: "",
    email: "",
    password: "",
    checkPassword: "",
  });

  const handleInput = (keyName) => (currentValue) => {
    setRegister({
      ...register,
      [keyName]: currentValue,
    });
  };

  return (
    <Container>
      <Row className="justify-content-center">
        <Col md={6} xl={8}>
          <Logo title="" />
          <form action="">
            <AuthInput
              value={register.account}
              onChange={handleInput("account")}
              placeholder=""
            />
            <AuthInput
              label=""
              value={register.name}
              onChange={handleInput("name")}
              placeholder=""
            />
            <AuthInput
              label="Email"
              type="email"
              value={register.email}
              onChange={handleInput("email")}
              placeholder="Email"
            />
            <AuthInput
              label=""
              type="password"
              autoComplete="new-password"
              value={register.password}
              onChange={handleInput("password")}
              placeholder=""
            />
            <AuthInput
              label=""
              type="password"
              autoComplete="new-password"
              value={register.checkPassword}
              onChange={handleInput("checkPassword")}
              placeholder=""
            />
            <div className="mb-3">
              <button className="btn btn-primary w-100 text-white rounded-pill">
                
              </button>
            </div>
          </form>

          <div className="d-flex justify-content-center">
            <Link to="/login"></Link>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default RegisterPage;
